.participantContainer{
    height: 100vh;
    background-color:rgba(0,0,0,0.1);
}

.participantHeading{
    padding-top:7.5rem;
    padding-bottom:0.55rem;
    text-align:center;
    font-size: 2.5rem;
    text-transform: uppercase;
    color: maroon;
}
.addParticipants{
    padding:1rem 2rem;
    background:none;
    border:none;
    outline: none;
    text-transform: uppercase;
    background-color: green;
    color:white;
    font-size:1.7rem;
    font-family:'Nunito', sans-serif;
    font-weight: 500;
    cursor: pointer;
}

.addParticipants:hover{
    box-shadow:0.2rem 0.2rem 0.2rem 0.2rem rgba(0,0,0,0.2); 
    
}
.addParticipants:active{
    background-color: white;
    border:1px solid darkgreen;
    color:green;
    
}

