.column{
    width:60vw;
    margin: 1rem auto;
    height: 6rem;
    display:flex;
    justify-content:space-around;
    align-items: center;
    box-shadow: 0 2px 3px #ccc;
    z-index: 20;
    background-color: white;
    
}
.column:hover{
    box-shadow: 0.25rem 0.25rem 0.25rem 0.25rem rgba(0,0,0,0.2) ;
}
.studentName{
   color:green;
   text-transform: uppercase;
   font-size:1.3rem;
   width:20rem;
   
}

.rollNo{
   color:green;
   font-size: 1.3rem;
   font-weight: 700;
}
.dereg{
    text-align: center;
    display: inline-block;
    background-color: red;
    color:white;
    border:none;
    outline: none;  
    width:7rem;
    height:3rem;
    padding: 0.5rem 0.7rem;
    font-size: 1rem;
    font-weight: 400;
    cursor: pointer;
    text-transform: uppercase;
    font-family:'Nunito', sans-serif;
    font-weight: 700;
    cursor: pointer;
    box-sizing: border-box;
}
.dereg:hover{
    box-shadow:.2rem .2rem .2rem .2rem rgba(0,0,0,0.2);
}
.dereg:active{
    color:red;
    border:1px solid darkred;
    background-color:white;

}
BsPersonFill{
    visibility:hidden ;
}