.cardimage{
    width:20rem;
    height:18rem;
    object-fit: cover;
    padding-top:0.6rem;
    margin-bottom:2rem;

}
.heading{
    color:green;
    font-weight: 700;
    text-transform: uppercase;
    font-size: 1.8rem;
    height:6.5rem;
   
    
}
.card{
    width: 24rem;
    height: 37rem;
    border: 2px solid #eee;
    box-shadow: 0 2px 3px #ccc;
    padding: 0.5rem;
    text-align:center;
    margin:4rem;
    z-index: 10;
    background-color: white;

}
.card:hover{
    box-shadow: 1rem 1rem 1rem 1rem rgba(0,0,0,0.2) ;
}
.credits{
    font-weight:700;
    font-size: 1.5rem;
    color:#C21481;

}
.semester{
    font-weight:700;
    font-size: 1.5rem;
    color:#C21481;
    text-transform: uppercase;

}

.classroom{
    font-weight:700;
    font-size: 1.5rem;
    color:#C21481;

}
a{
    text-decoration: none;
}